import { Component, h, Prop, Host, FunctionalComponent, Fragment, Listen } from '@stencil/core';
import ChevronRight from '@material-design-icons/svg/outlined/chevron_right.svg';
import ChevronLeft from '@material-design-icons/svg/outlined/chevron_left.svg';
import { ButtonTheme, ButtonType, ButtonWidth } from '../../../interfaces/components/basic/button/button';
import { FileUtils } from '../../../utils/data/file';

/**
 * @slot icon - Slot for the custom icon html.
 * @slot default - Slot for the content of the button
 */
@Component({
  tag: 'rwc-button',
  styleUrl: 'button.scss',
  shadow: true
})
export class RwcButton {

  /**
   * The reference to the container element.
   */
  private containerElement: HTMLElement;

  /**
   * Path to the right icon.
   */
  @Prop() iconRight: string = '';

  /**
   * Set right icon visible.
   */
  @Prop() showIconRight: boolean = false;

  /**
  * Path to the left icon.
  */
  @Prop() iconLeft: string = '';

  /**
  * Set left icon visible.
  */
  @Prop() showIconLeft: boolean = false;

  /**
  * Set button type.
  */
  @Prop({ reflect: true }) type: ButtonType = 'filled';

  /**
  * Set button theme.
  */
  @Prop({ reflect: true }) theme: ButtonTheme = 'default';

  /**
  * Set button width.
  */
  @Prop() width: ButtonWidth = 'auto';

  /**
   * Set animation.
   */
  @Prop() ripple: boolean = false;

  /**
   * Set rendering of box-shadow.
   */
  @Prop() renderShadow: boolean = true;

  /**
   * Creates ripple animation on click.
   */
  @Listen('click')
  onButtonClick(event: MouseEvent): void {
    if (this.ripple) {
      this.createRipple(event);
    }
  }

  /**
   * Renders the ripple animation.
   */
  private createRipple(event: MouseEvent): void {
    const circle = document.createElement('span');
    const diameter = Math.max(this.containerElement.clientWidth, this.containerElement.clientHeight);
    const radius = diameter / 2;

    circle.style.width = circle.style.height = `${ diameter }px`;
    circle.style.left = `${ event.clientX - (this.containerElement.getBoundingClientRect().left + window.scrollX + radius) }px`;
    circle.style.top = `${ event.clientY - (this.containerElement.getBoundingClientRect().top + window.scrollY + radius) }px`;
    circle.classList.add('ripple');

    const ripple = this.containerElement.querySelector('.ripple');

    if (ripple) {
      ripple.remove();
    }

    this.containerElement.appendChild(circle);
  }

  /**
   * Renders the component.
   */
  render(): FunctionalComponent {
    return (
      <Host>
        <div
          ref={ (element: HTMLElement) => this.containerElement = element }
          class={ {
            container: true,
            shadow: this.renderShadow,
            [`type-${ this.type }`]: true,
            [`theme-${ this.theme }`]: true,
            [`width-${ this.width }`]: true,
          } }>
          <div class="icon-wrapper">
            { this.renderLeftIconWrapper() }
          </div>
          <div class="text">
            <slot></slot>
          </div>
          <div class="icon-wrapper">
            { this.renderRightIconWrapper() }
          </div>
        </div>
      </Host>
    );
  }

  /**
   * Renders the right icon wrapper.
   */
  private renderRightIconWrapper(): FunctionalComponent {
    if (this.showIconRight) {
      return (
        <Fragment>
          <slot name="icon">
            { this.renderRightIcon() }
          </slot>
        </Fragment >
      )
    }
  }

  /**
  * Renders the left icon wrapper.
  */
  private renderLeftIconWrapper(): FunctionalComponent {
    if (this.showIconLeft) {
      return (
        <Fragment>
          <slot name="icon">
            { this.renderLeftIcon() }
          </slot>
        </Fragment >
      )
    }
  }

  /**
   * Renders the right icon element.
   */
  private renderRightIcon(): FunctionalComponent {
    if (this.iconRight) {
      return (
        <Fragment>
          <img class="icon" src={ this.iconRight } alt={ FileUtils.getCleanFileName(this.iconRight) }></img>
        </Fragment>
      )
    }
    return (
      <Fragment>
        <div innerHTML={ ChevronRight } class="icon svg-icon"></div>
      </Fragment>
    )
  }

  /**
   * Renders the left icon element.
   */
  private renderLeftIcon(): FunctionalComponent {
    if (this.iconLeft) {
      return (
        <Fragment>
          <img class="icon" src={ this.iconLeft } alt={ FileUtils.getCleanFileName(this.iconLeft) }></img>
        </Fragment>
      )
    }
    if (this.showIconLeft) {
      return (
        <Fragment>
          <div innerHTML={ ChevronLeft } class="icon svg-icon">
          </div>
        </Fragment>
      )
    }
  }
}
