@import 'global.scss';

:host {
  /**
  * @prop --button-border-width: The border width of outline button.
  * @prop --button-border-radius: The border radius of the button.
  * @prop --button-border-color: The color of the border of the button.
  * @prop --button-padding: The top-bottom and left-right padding for button.
  * @prop --button-box-shadow: The shadow of the button.
  * @prop --button-icon-size: The size of the icon of the button.
  * @prop --button-icon-color: The color of the icon of the button.
  * @prop --button-width: The width of the button.
  * @prop --button-background-color: The background color of the button.
  * @prop --button-text-color: The color of the text of the button.
  * @prop --button-background-color-hover: The color of the button on hover.
  * @prop --button-outline-color: The coolor of the outline of the button.
  * @prop --button-outline-style: The style of the outline of the button.
  * @prop --button-background-color-type-outline-hover: The background color of the outline type button on hover.
  * @prop --button-ripple-animation: The click animation of the button.
  */
  --button-border-width: 2px;
  --button-border-radius: #{$rwc-spacing--2xs};
  --button-border-color: #{$rwc-color-green-700};
  --button-padding: #{$rwc-spacing--xs} #{$rwc-spacing--sm};
  --button-box-shadow: #{$rwc-box-shadow-md};
  --button-icon-size: 1.5rem;
  --button-icon-color: #{$rwc-color-green-700};
  --button-width: fit-content;
  --button-background-color: #{$rwc-color-green-700};
  --button-text-color: #{$rwc-color-text-primary};
  --button-background-color-hover: #{$rwc-color-green-800};
  --button-outline-color: #{$rwc-color-outline-default};
  --button-outline-style: solid;
  --button-background-color-type-outline-hover: #{$rwc-color-grey-700};
  --button-ripple-animation: #{$rwc-color-ripple};

  display: block;
}

.theme-blue {
  --button-background-color: #{$rwc-color-blue-500};
  --button-border-color: #{$rwc-color-blue-500};
  --button-text-color: #{$rwc-color-grey-100};
  --button-background-color-hover: #{$rwc-color-blue-600};
  --button-outline-color: #{$rwc-color-outline-blue};
  --button-red-outline-style: var(--button-outline-style);
}

.theme-dark {
  --button-background-color: #{$rwc-color-grey-900};
  --button-border-color: #{$rwc-color-grey-900};
  --button-text-color: #{$rwc-color-grey-100};
  --button-background-color-hover: #{$rwc-color-grey-800};
  --button-outline-color: #{$rwc-color-outline-dark};
  --button-dark-outline-style: var(--button-outline-style);
}

.theme-red {
  --button-background-color: #{$rwc-color-red-700};
  --button-border-color: #{$rwc-color-red-700};
  --button-text-color: #{$rwc-color-grey-100};
  --button-background-color-hover: #{$rwc-color-red-800};
  --button-outline-color: #{$rwc-color-outline-red};
  --button-red-outline-style: var(--button-outline-style);
}

.theme-orange {
  --button-background-color: #{$rwc-color-orange-600};
  --button-border-color: #{$rwc-color-orange-600};
  --button-text-color: #{$rwc-color-grey-100};
  --button-background-color-hover: #{$rwc-color-orange-700};
  --button-outline-color: #{$rwc-color-outline-orange};
  --button-red-outline-style: var(--button-outline-style);
}

.container {
  position: relative;
  overflow: hidden;
  align-items: center;
  border-radius: var(--button-border-radius);
  display: flex;
  padding: var(--button-padding);
  transition: border-color 400ms ease, background-color 400ms ease, color 400ms ease, fill 400ms ease;
  cursor: pointer;
}

.ripple {
  position: absolute;
  border-radius: 50%;
  transform: scale(0);
  animation: ripple 600ms linear;
  background-color: var(--button-ripple-animation);
}

.type-filled {
  background-color: var(--button-background-color);
  border-color: var(--button-border-color);
  color: var(--button-text-color);
  transition: border-color 400ms ease, background-color 400ms ease;

  &:hover {
    background-color: var(--button-background-color-hover);
    border-color: var(--button-background-color-hover);
  }

  &:active {
    outline-color: var(--button-outline-color);
    outline-style: var(--button-outline-style);
  }
}

.type-outline {
  border-color: var(--button-border-color);
  color: var(--button-border-color);
  transition: color 200ms ease, background-color 200ms ease;

  .icon-wrapper {
    svg {
      path {
        fill: var(--button-border-color);
        transition: fill 200ms ease;
      }
    }
  }

  &:hover {
    background-color: var(--button-background-color);
    color: var(--button-text-color);

    .icon-wrapper {
      svg {
        path {
          fill: var(--button-text-color);
        }
      }
    }
  }

  &:active {
    outline-color: var(--button-outline-color);
    outline-style: var(--button-outline-style);
  }
}

.shadow {
  box-shadow: var(--button-box-shadow);
}

.icon-wrapper {
  align-self: center;
  flex-shrink: 0;
  display: flex;

  svg {
    height: 100%;
    width: 100%;

    path {
      fill: var(--button-text-color);
    }
  }
}

.icon {
  height: var(--button-icon-size);
  width: var(--button-icon-size);
}

//Set border
.type-filled,
.type-outline {
  border-width: var(--button-border-width);
  border-style: solid;
}

.width-auto {
  width: var(--button-width);
}

.width-full {
  width: unset;
  justify-content: space-between;
}

@keyframes ripple {
  to {
    transform: scale(4);
    opacity: 0;
  }
}
